import React from 'react';

import './projects.style.scss';

import Project_data from './projects.data';

import Project from '../../components/project/project.comp';

class Projects extends React.Component{
    constructor(){
        super()

        this.state = {
            projects: []
        }
    }

    componentDidMount(){
        this.setState({projects: Project_data});
    }

    render(){
        return(
            <div className="projects">
                {this.state.projects.map(({id, name, url, about, link}) =>{
                    return <Project key={id} name={name} url={url} about={about} link={link} />
                })}
            </div>
        )
    }
}

export default Projects;