import React from 'react';

import './about.style.scss';

import Logo from '../../assets/MyPicCroped.jpg';

import Skill from '../../components/skill/skill.comp';

import Icon_data from './about.data';

class About extends React.Component{
    constructor(){
        super()

        this.state = {
            icons: []
        }
    }

    componentDidMount(){
        this.setState({icons: Icon_data});
    }

    render(){
        const {icons} = this.state;
        return(
            <div className="about">
                <h1 className="aboutH">About Me</h1>
                <div className="aboutTop">
                    <div className="intro">
                        <div className="photoName">
                            <img className="photo" src={Logo} alt="pictur" />
                            <span className="name">Matas</span>
                        </div>
                        
                        <div className="text">
                            <h1 className="quote">“Give a man a program, frustrate him for a day.<br/>Teach a man to program, frustrate him for a lifetime.”</h1>
                            {/* <h1 className="spec">Specialities: Front-end/JavaScript/React/Fullstack developer</h1> */}
                        </div>
                    </div>

                    <div className="intro2">
                        <div className="intro2text">
                        <h1 className="hello">Hello,</h1>
                            <h1>My name is Matas. Currently I'm a Software Engineering Student.<br/>But also actively trying to improve my skills, learn new things<br/> related to web development. </h1>
                        </div>
                        
                    </div>
            
                </div>

                <div className="skills">
                    <h1 className="header">Skills</h1>
                    <div className="logos">
                        {icons.map(item => {
                            return (<Skill key={item.id} url={item.url} />)
                        })}

                    </div>
                </div>

            </div>
        )
    }
}

export default About;