import React from 'react';

import ContactCom from '../../components/contact_comp/contact.comp';

import './contact.style.scss';

class Contact extends React.Component{
    constructor(){
        super()

        this.state = {
            links: [
                {
                    url:"https://www.linkedin.com/in/matas-jarilinas-565949184",
                    img: require("../../assets/logos/linkedin-original-wordmark.svg"),
                    name: "Linked.in",
                    id: 1
                }
            ]
        }
    }

    render(){
        return(
            
            <div className="contact">
                <h1>Contact</h1>
                <div className="content">
                    {this.state.links.map(link => <ContactCom key={link.id} name={link.name} url={link.url} img={link.img} />)}
                </div>
                <div className="email">
                <h1>Email me</h1>
                    <div className="form">
                        <form id="fs-frm" name="simple-contact-form" acceptCharset="utf-8" action="https://formspree.io/matas.jarilinas@stud.vgtu.lt" method="post">
                            <fieldset id="fs-frm-inputs">
                                <label htmlFor="full-name">Full Name</label>
                                <input type="text" name="name" id="full-name" placeholder="First and Last" required=""/>
                                <label htmlFor="email-address">Email Address</label>
                                <input type="email" name="_replyto" id="email-address" placeholder="Email" required=""/>
                                <label htmlFor="message">Message</label>
                                <textarea rows="2" name="message" id="message" placeholder="Your message here." required=""></textarea>
                                <input type="hidden" name="_subject" id="email-subject" value="Contact Form Submission"/>
                            </fieldset>
                            <input className="submit" type="submit" value="Submit"/>
                        </form>
                    </div>
                </div>
            </div>
             
        )
    }
}

export default Contact;