
import React from 'react';

import './navBar.style.scss';

import {Link} from 'react-router-dom';

import Logo from '../../assets/logo3.png';

class NavBar extends React.Component{
    constructor(){
        super()

        this.state = {
            tab: window.location.hash
        }
    }

    render(){
        return(
            <div className="navBar">
                <div onClick={()=>this.setState({tab:"#/"})} id={(this.state.tab === "#/") ? "hold" : null } className="logo pad">
                    <Link onClick={this.setHold} to="/"><img src={Logo} alt="Logo"/></Link>
                </div>
        
                <div className="pad items">
                    <Link to="/about"><span onClick={()=>this.setState({tab:"#/about"})} id={(this.state.tab === "#/about") ? "hold" : null } className="item">About</span></Link>

                    <Link to="/projects"><span onClick={()=>this.setState({tab:"#/projects"})} id={(this.state.tab === "#/projects") ? "hold" : null } className="item">Projects</span></Link>
                    
                    <Link to="/contact"><span onClick={()=>this.setState({tab:"#/contact"})} id={(this.state.tab === "#/contact") ? "hold" : null } className="item">Contact</span></Link>
                </div>
            </div>
        )
    }
}

export default NavBar;